@import "../../../styles/base";
@import "../../../styles/animation.scss";
.desk {
	position: absolute;
	left: 0;
	top: 0;
	width: 100vw;
	z-index: -1;
}
.second {
	&_title {
		position: relative;
		top: -20px;
		.dots_white {
			width: 220px;
			position: absolute;
			left: 0;
			top: -60px;
		}

		&_text {
			padding-left: 5rem;
			// text-align: right;
			font-size: 2.5rem;
			font-weight: 400;
			font-family: "Caveat";
			color: $font-gray;
			animation: fadeIn 2s;
			z-index: 2;
		}
	}
}
